
export default {
  discoverList (data) {
    let discoverList = []
	for (let i = 1; i <= 20; i++) {
		discoverList.push({id: i, name: '' + i, avatar: '/static/icon/avatar.png',
			msg: '', time: this.timeToString('2020/09/20 16:05')})
	}
	
    data.list = discoverList
  },
  
  timeToString(time) {
  	let currentTime = Date.parse(new Date());
  	let dateTime = time;//
  	let d_day = Date.parse(new Date(dateTime));
  	let day = Math.abs(parseInt((d_day - currentTime)/1000/3600/24));//
  	let hour = Math.abs(parseInt((d_day - currentTime)/1000/3600));//
  	let minutes = Math.abs(parseInt((d_day - currentTime)/1000/60));//
  	let seconds = Math.abs(parseInt((d_day - currentTime)/1000));//
  	if(day >= 2){
  		return day + ""
  	}else if(day > 0 && day < 2){
  		return ""
  	}else if(hour > 0 && hour < 24){
  		return hour + ""
  	}else if(minutes > 0 && minutes < 60){
  		return minutes + ""
  	}else if(seconds > 0 && seconds < 60){
  		return '';
  	}
  }
}
