
export default {
  chatList (data) {
    let chatList = []
	for (let i = 1; i <= 20; i++) {
		chatList.push({id: i, name: '' + i, avatar: '/static/icon/avatar.png',lastMsg: '' + i, lastTime: this.timeToString('2020/09/18 12:37'), unread: i%3})
	}
	
    data.list = chatList
  },
  timeToString(time) {
	let now = new Date()
	let dateTime = new Date(time)
	
 	if (now.toDateString() === dateTime.toDateString()) {
		let hour = dateTime.getHours()
		let minute = dateTime.getMinutes()
		if (hour < 5) {
			return '' + hour + ':' + minute
		} else if (hour >= 5 && hour < 12) {
			return '' + hour + ':' + minute
		} else if (hour === 12) {
			return '' + hour + ':' + minute
		} else if (hour > 12 && hour < 19) {
			return '' + (hour - 12) + ':' + minute
		} else if (hour >= 19 && hour < 24) {
			return '' + (hour - 12) + ':' + minute
		} 
	}
	
	let yesterday = new Date(now.getTime()-24*60*60*1000)
	
	if (yesterday.toDateString() === dateTime.toDateString()) {
		return ''
	}	
	
	let nowWeekDay = now.getDay()
	let today = new Date(now.toLocaleDateString()).getTime()
	if (nowWeekDay == 0) nowWeekDay = 7
	if (today - (nowWeekDay - 1)*24*60*60*1000 < dateTime.getTime()) {
		let weekStr = ['', '', '', '', '', '', '']
		return '' + weekStr[dateTime.getDay()]
	}
	
	let year = dateTime.getFullYear()
	let nowYear = now.getFullYear()
	let dayNo = dateTime.getDate()
	let month = dateTime.getMonth() + 1

	if (nowYear != year) {
		return year + '' + '' + month + '' + dayNo + ''
	} else {
		return month + '' + dayNo + ''
	}	
  }
}
